import React, { Fragment } from 'react'
import { Like, Comments } from 'react-facebook';
import { withRouter } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import { isServer } from './../../../utilities/helpers';

export default withRouter((props) => {
    const createUrl = () => {
        let url = `https://www.iambican.com`;
        const { link = '', location: { pathname = '/' } } = props;
        url = url + pathname;
        // Fallback to hardcoded just in case
        return link !== '' ? link : url;
    }

    const faComments = {
        icon: [
            512,
            512,
            [],
            'f4ad',
            'M128 216c-13.3 0-24 10.7-24 24s10.7 24 24 24 24-10.7 24-24-10.7-24-24-24zm128 0c-13.3 0-24 10.7-24 24s10.7 24 24 24 24-10.7 24-24-10.7-24-24-24zm128 0c-13.3 0-24 10.7-24 24s10.7 24 24 24 24-10.7 24-24-10.7-24-24-24zM256 32C114.6 32 0 125.1 0 240c0 47.6 19.9 91.2 52.9 126.3C38 405.7 7 439.1 6.5 439.5c-6.6 7-8.4 17.2-4.6 26S14.4 480 24 480c61.5 0 110-25.7 139.1-46.3C192 442.8 223.2 448 256 448c141.4 0 256-93.1 256-208S397.4 32 256 32zm0 384c-28.3 0-56.3-4.3-83.2-12.8l-15.2-4.8-13 9.2c-23 16.3-58.5 35.3-102.6 39.6 12-15.1 29.8-40.4 40.8-69.6l7.1-18.7-13.7-14.6C47.3 313.7 32 277.6 32 240c0-97 100.5-176 224-176s224 79 224 176-100.5 176-224 176z'
        ],
        iconName: 'comment-dots',
        prefix: 'fal'
    };

    library.add(faComments);

    const socialUrl = createUrl();

    return (
        <div className="portfolio-social flex-grow-1 position-relative" id="trigger-pin-social">
            <div className="social social--facebook" id="pin-social">
                <h3 className="lead mt-5">
                    <i className="fal fa-comment-dots"></i>
                    <span> Be Social :)</span>
                </h3>
                <hr />
                {!isServer && <Fragment>
                    <Like href={socialUrl} showFaces share />
                    <Comments href={socialUrl} width="100%" />
                </Fragment>}
            </div>
        </div>
    );
});